import { Transition } from "@headlessui/react";
import { Fragment, useState } from "react";

const Episode = ({ item, selectEpisode }) => {
  const [isHoverEpisode, setIsHoverEpisode] = useState(false);
  const [isHoverShow, setIsHoverShow] = useState(false);
  const [isHoverDownload, setIsHoverDownload] = useState(false);

  return (
    <div className="max-w-[482px] mx-auto">
      <div>
        <div
          onClick={() => (item.release ? selectEpisode(item) : {})}
          className="w-full relative cursor-pointer"
          onMouseEnter={() => setIsHoverEpisode(true)}
          onMouseLeave={() => setIsHoverEpisode(false)}
        >
          <img
            src={item.imageSlider}
            className={!item.release ? "grayscale brightness-150" : ""}
            alt={`preview-${item.id}`}
          />
          <Transition
            as={Fragment}
            show={item.release && isHoverEpisode}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95"
          >
            <img
              src="img/play-player.webp"
              alt="play btn"
              className="w-[100px] absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"
            />
          </Transition>

          {!item.release && item.releaseDate && (
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-white text-4xl uppercase">
              {item.releaseDate}
            </div>
          )}
        </div>
        <h3 className="text-3xl text-[#443b30] mt-5">{`${item.id}. ${item.title}`}</h3>
        <p className="text-[22px] text-[#a39c94]">{item.description}</p>
      </div>
      {item.release && (
        <div className="mt-4 flex gap-2">
          <button
            onClick={() => selectEpisode(item)}
            onMouseEnter={() => setIsHoverShow(true)}
            onMouseLeave={() => setIsHoverShow(false)}
            className={`bg-no-repeat py-1 pl-9 pr-4 text-[22px] transition-all ${
              isHoverShow ? "text-white" : "text-[#a39c94]"
            }`}
            style={{
              backgroundImage: isHoverShow
                ? "url(img/watch-hover.webp)"
                : "url(img/watch-rest.webp)",
            }}
          >
            Смотреть
          </button>
          <a
            rel="noreferrer"
            href="[rutor.is]Spider.Man.1994.S01.D.Studio616.torrent"
            target="_blank"
            onMouseEnter={() => setIsHoverDownload(true)}
            onMouseLeave={() => setIsHoverDownload(false)}
            className={`bg-no-repeat py-1 pl-9 pr-4 text-[22px] transition-all ${
              isHoverDownload ? "text-white" : "text-[#a39c94]"
            }`}
            style={{
              backgroundImage: isHoverDownload
                ? "url(img/download-hover.webp)"
                : "url(img/download-rest.webp)",
            }}
          >
            Скачать
          </a>
        </div>
      )}
    </div>
  );
};

export default Episode;
